.top-bar {
  padding-block: 24px;
  display: flex;
  justify-content: space-between;
  align-items: center;

  &__logo-link {
    display: flex;
  }

  &__logo {
    height: 38px;

    @include hover(
      (
        transform: scale(1.2),
      )
    );

    @include on-tablet {
      height: 56px;
    }
  }

  &__icons {
    display: flex;
    gap: 24px;
  }
}
