.product {
  @include hover(
    (
      transform: scale(1.05),
    )
  );

  cursor: pointer;

  &__photo {
    width: 100%;
    margin-bottom: 32px;
  }

  &__title {
    font-weight: 700;
    font-size: 24px;
    line-height: 33.6px;
    text-align: left;
    margin-bottom: 8px;
  }

  &__description {
    font-weight: 400;
    font-size: 16px;
    line-height: 22.4px;
    text-align: left;
    margin-bottom: 16px;
  }

  &__price {
    font-weight: 700;
    font-size: 24px;
    line-height: 33.6px;
    text-align: left;
  }
}
